	module.exports = {"data":{"profile":{"id":"28721457-40eb-5054-9738-eadd57feca27","content":"<p><div class=\"content-block  text-center\"><p>Bryan (aka Deeb) started playing the trumpet in fourth grade and studied with Vincent Macrina through high school in Brockton, MA. Bryan then studied with Jeff Holmes and the late Walter Chesnut while attending the University of Massachusetts at Amherst, from which he graduated Magna Cum Laude. While at UMass, Bryan played lead trumpet in Jazz Ensemble I, and received both an Outstanding Musician Award from the International Association of Jazz Educators and a Downbeat Award from Downbeat Magazine.</p></div></p>\n<p><div class=\"content-block  text-center\"><p>As college was winding up, he found the funk and joined Rippopotamus, a Boston-based funk/rock band. During Bryan&#8217;s tenure with Rippo, the band opened for a variety of national acts, including Queen Latifah, Phish, Fishbone, De La Soul, and the Beastie Boys. Rippo released five albums, wrote and performed the theme song for MTV&#8217;s dating show Singled Out, and were a featured act at the Montreal Jazz Festival. They also won a 1996 Boston Music Award.</p></div></p>\n<p><div class=\"content-block  text-center\"><p>In 2001, Bryan founded Occasional Brass &amp; Strings, a Boston-based company offering brass, strings, woodwinds, and more for ceremonies, cocktails, etc. OB&amp;S has partnered with C-Zone to bring you the finest musicians in the Boston area.</p></div></p>\n<p><div class=\"content-block  text-center\"><p>An all-around player, and in addition to his work with Occasional Brass, Bryan can be heard around Boston playing weddings, musicals, funk, rock, big band, dixieland, salsa/merengue, and even a circus gig or two.</p></div></p>\n","title":"Bryan Hilliard","acf":{"title":"Trumpets, Flugelhorn","video":null,"facebook":"","instagram":"","testimonials":""},"image":{"localFile":{"childImageSharp":{"thumbnail":{"base64":"data:image/jpeg;base64,","aspectRatio":0.5911330049261084,"src":"/static/c79ef18a2b1ffc86dcc234924c46eaa0-cb85f20d6c92528a3f35e01d09ea94b8-43613.jpg","srcSet":"/static/c79ef18a2b1ffc86dcc234924c46eaa0-cb85f20d6c92528a3f35e01d09ea94b8-98eb5.jpg 110w,\n/static/c79ef18a2b1ffc86dcc234924c46eaa0-cb85f20d6c92528a3f35e01d09ea94b8-d8591.jpg 220w,\n/static/c79ef18a2b1ffc86dcc234924c46eaa0-cb85f20d6c92528a3f35e01d09ea94b8-43613.jpg 240w","sizes":"(max-width: 240px) 100vw, 240px","originalImg":"/static/c79ef18a2b1ffc86dcc234924c46eaa0-cb85f20d6c92528a3f35e01d09ea94b8-43613.jpg"},"full":{"src":"/static/c79ef18a2b1ffc86dcc234924c46eaa0-cb85f20d6c92528a3f35e01d09ea94b8-7af24.jpg","height":406,"width":240,"srcSet":"/static/c79ef18a2b1ffc86dcc234924c46eaa0-cb85f20d6c92528a3f35e01d09ea94b8-7af24.jpg 1x"}}}},"yoast":{"title":"","canonical":"","metaDescription":"","linkdex":"","metakeywords":"","noIndex":"","noFollow":"","meta_robots_adv":"","metaKeywords":"","redirect":"","ogTitle":"","ogDescription":"","ogImage":"","twitterTitle":"","twitterDescription":"","twitterImage":""}},"site":{"id":"Site","siteMeta":{"title":"C-Zone Entertainment","subtitle":"Premiere DJs & Musicians | Servicing Greater Boston & New England | 617.320.0723"}}},"pathContext":{"id":"28721457-40eb-5054-9738-eadd57feca27"}}

